html, body { margin:0; padding:0; overflow:hidden }
.svg{
  border-style:dashed;
  background-color:azure;
  /* position:fixed; top:0; bottom:0; left:0; right:0 */
  position: relative;
}

.country{
  stroke : blue;
  stroke-width: 0.1;
  cursor: pointer;
}
.country:hover{
  stroke-width: 0.5;
  stroke: black;
}